export const translateInfoKeys = (key: string) => {
  switch (key) {
    case 'publish': {
      return '';
    }
    case 'issueYear': {
      return ' ';
    }
    case 'pages': {
      return '';
    }
    case 'cover': {
      return '';
    }
    case 'format': {
      return '';
    }
    case 'categories': {
      return '';
    }
    case 'weight': {
      return '';
    }
    case 'ISBN': {
      return 'ISBN';
    }
    case 'producer': {
      return '';
    }
    default: {
      return '';
    }
  }
};
